/* Mobile Version */
@media screen and (max-width: 550px){
  .mob-scroll-row-h180{
    overflow-x:scroll;
    width: 100%;
    flex-wrap: nowrap !important;
    max-height: 180px;
  }
  .mob-scroll::-webkit-scrollbar {
    width: 5px;
  }
  .mob-scroll-item-h180{
    max-width: 150px !important;
    justify-content: center;
    text-align: center;
    /* height: 160px; */
  }
  .mob-scroll-row-h330{
    overflow-x:scroll;
    width: 100%;
    flex-wrap: nowrap !important;
    max-height: 530px;
    margin-right: 20px !important;
  }
  .mob-scroll-item-w250{
    max-width: 250px !important;
    justify-content: center;
    text-align: center;
  }

   /* OVERRIDE */
  .specials .special {
    margin-right: 20px !important;
  }
  

}
/* CHAT CONTAINER */
.j-tab{
  /* background-color: #ebebeb; */
  font-size: 18px;
  font-weight: bold;
  border-radius: 10px;
  padding-right: 10px;
  padding-left: 10px;
  padding-bottom: 2px;
  padding-top: 2px;
}
.j-tab-active{
  background-color: #dcdefd;
  font-size: 18px;
  font-weight: bold;
  border-radius: 10px;
  padding-right: 10px;
  padding-left: 10px;
  padding-bottom: 2px;
  padding-top: 2px;
}
.chat_page .container{max-width:100%; margin:auto;}
.chat_page img{ max-width:100%;}
.chat_page .inbox_people {
  background: #f8f8f8 none repeat scroll 0 0;
  float: left;
  overflow: hidden;
  width: 40%; border-right:1px solid #c4c4c4;
}
.chat_page .inbox_msg {
  border: 1px solid #c4c4c4;
  clear: both;
  overflow: hidden;
}
.chat_page .top_spac{ margin: 20px 0 0;}


.chat_page .recent_heading {float: left; width:40%;}
.chat_page .srch_bar {
  display: inline-block;
  text-align: right;
  width: 60%;
}
.chat_page .headind_srch{ padding:10px 29px 10px 20px; overflow:hidden; border-bottom:1px solid #c4c4c4;}

.chat_page .recent_heading h4 {
  color: #05728f;
  font-size: 21px;
  margin: auto;
}
.chat_page .srch_bar input{ border:1px solid #cdcdcd; border-width:0 0 1px 0; width:80%; padding:2px 0 4px 6px; background:none;}
.chat_page .srch_bar .input-group-addon button {
  background: rgba(0, 0, 0, 0) none repeat scroll 0 0;
  border: medium none;
  padding: 0;
  color: #707070;
  font-size: 18px;
}
.chat_page .srch_bar .input-group-addon { margin: 0 0 0 -27px;}
.chat_page .chat-product-img{
  width: 40px;
  height: 40px;
  object-fit: cover;
}
.chat_page .chat_ib h5{ font-size:15px; color:#464646; margin:0 0 8px 0;}
.chat_page .chat_ib h5 span{ font-size:13px; float:right;}
.chat_page .chat_ib p{ font-size:14px; color:#989898; margin:auto}
.chat_page .chat_img {
  float: left;
  width: 12%;
}
.chat_page .chat_img img{
  border-radius: 40px;
  object-fit: cover;
}
.chat_page .chat_ib {
  float: left;
  padding: 0 0 0 15px;
  width: 88%;
}

.chat_page .chat_people{ overflow:hidden; clear:both;}
.chat_page .chat_list {
  border-bottom: 1px solid #c4c4c4;
  margin: 0;
  padding: 18px 16px 10px;
}
.chat_page .inbox_chat { height: 550px; overflow-y: scroll;}

.chat_page .active_chat{ background:#c3e4ff;}
.chat_page .incoming_msg{
  padding-bottom: 23px !important;
}
.chat_page .incoming_msg_img {
  display: inline-block;
  width: 6%;
  
}
.chat_page .incoming_msg_img img{
  border-radius: 50px;
}
.chat_page .received_msg {
  display: inline-block;
  padding: 0 0 0 10px;
  vertical-align: top;
  width: 92%;
 }
 .chat_page .received_withd_msg p {
  background: #ebebeb none repeat scroll 0 0;
  border-radius: 3px;
  color: #646464;
  font-size: 14px;
  margin: 0;
  padding: 5px 10px 5px 12px;
  width: 100%;
}
.chat_page .time_date {
  color: #747474;
  display: block;
  font-size: 12px;
  margin: 8px 0 0;
}
.chat_page .received_withd_msg { width: 57%;}
.chat_page .mesgs {
  float: left;
  padding: 30px 15px 0 25px;
  width: 60%;
}

.chat_page .sent_msg p {
  background: #05728f none repeat scroll 0 0;
  border-radius: 3px;
  font-size: 14px;
  margin: 0; color:#fff;
  padding: 5px 10px 5px 12px;
  width:100%;
}
.chat_page .outgoing_msg{ overflow:hidden; margin:26px 0 26px;}
.chat_page .sent_msg {
  float: right;
  width: 46%;
}
.chat_page .input_msg_write input {
  background: rgba(0, 0, 0, 0) none repeat scroll 0 0;
  border: medium none;
  color: #4c4c4c;
  font-size: 15px;
  min-height: 48px;
  width: 100%;
}

.chat_page .type_msg {border-top: 1px solid #c4c4c4;position: relative;}
.chat_page .msg_send_btn {
  background: #05728f none repeat scroll 0 0;
  border: medium none;
  border-radius: 50%;
  color: #fff;
  cursor: pointer;
  font-size: 17px;
  height: 33px;
  position: absolute;
  right: 0;
  top: 11px;
  width: 33px;
}
.chat_page .messaging { padding: 0 0 50px 0;}
.chat_page .msg_history {
  height: 516px;
  overflow-y: auto;
}
.profile-page .profile-image{
  width: 100%;
  text-align: center;
}
.profile-page .profile-image img{
  border-radius: 100px;
  width: 120px;
  height: 120px;
  object-fit: cover;
}
/* CHAT CONTAINER END */
